import React from 'react';
import { Container, Grid } from 'semantic-ui-react';
import ExperienceCard from './ExperienceCard';
import ProjectCard from './ProjectCard';

export default class ExperienceSection extends React.Component {
	constructor(props) {
		super(props);
		this.experiences = props.experiences;
		this.projects = props.projects;
	}
	render() {
		// const { experiences, projects } = this.state;
		return (
			<Container text>
				<a name="Experience" />
				{/* <h1>Experience</h1> */}
				<Grid stackable columns={2}>
					<Grid.Column>
						<Container>
							<h3>Work Experience</h3>
							{this.props.experiences.map((job) => <ExperienceCard {...job.fields} />)}
						</Container>
					</Grid.Column>
					<Grid.Column>
						<Container>
							<h3>Projects - some things I built</h3>
							{this.props.projects.map((project) => <ProjectCard {...project.fields} />)}
						</Container>
					</Grid.Column>
				</Grid>
			</Container>
		);
	}
}

